html,
.content-output ol,
.content-output ul,
.content-output p {
  font-family: "Plaid S", BlinkMacSystemFont, -apple-system, Roboto, Arial,
    sans-serif;
}
.font-plaid-m,
.heading.heading_h4,
.heading.heading_h5,
.button,
.post-content h3,
.post-content h4,
.post-content h5,
.content-output ol > li:before {
  font-family: "Plaid M", BlinkMacSystemFont, -apple-system, Roboto, Arial,
    sans-serif;
}

.heading.heading_h1,
.heading.heading_h2,
.heading.heading_h3,
.post-content h1,
.post-content h2 {
  font-family: "Plaid M Mono", BlinkMacSystemFont, -apple-system, Roboto, Arial,
    sans-serif;
}
